import React from 'react';
import { Form, Input, Col, Button, Select, message  } from 'antd';
import URL from '../serverAPI.config';
import ComponePictures from './comp-pictur';
const FormItem = Form.Item;
const Option = Select.Option;

class CompForm extends React.Component {
  constructor () {
    super();//
    this.state = {
      attrlist: [],
      imageIdList: []
    };
  }
  componentDidMount () {
    const myFetchOptions = {
      method: 'GET',
      mode : 'cors',
    };
    fetch(URL.attributeList,myFetchOptions)
      .then(res=>res.json().then(json=>{
        console.log(':', json);
        for (var i = 0; i < json.length;i++) {
          this.state.attrlist.push(<Option key={json[i]._id.toString()}>{json[i].callname}</Option>)
        }
      }))
  }

  _handleChildValueChange (fileIdList) {
    this.setState({ imageIdList: fileIdList })
    console.log('fileIdList:', fileIdList);
  }

  _handleReset () {
    this.props.form.resetFields();
  }

  _checkNname (rule, value, callback) {
    console.log(value)
    const data = {name:value}
    const myFetchOptions = {
      method: 'POST',
      headers: {'Content-Type':'application/json'},
      mode : 'cors',
      body: JSON.stringify(data)
    };
    fetch(URL.checkComponent,myFetchOptions)
      .then((res) => {
        if (res.status == 202) {
          callback('');
        } else {
          callback();
        }
      })
  }

  _handleSubmit (e) {
    e.preventDefault();//
    this.props.form.validateFields((err, values) => {
      if (!err) {
        const data = {
          id:values.componentId,
          name:values.componentName,
          describe:values.componentDescribe,
          attribute:values.componentAttr,
          images:this.state.imageIdList
        }
        const myFetchOptions = {
          method: 'POST',
          headers: {'Content-Type':'application/json'},
          mode : 'cors',
          body: JSON.stringify(data)
        };
        if (this.state.imageIdList.length >= 1 ) {
          fetch(URL.newComponent,myFetchOptions)
            .then((res) => {
              if (res.ok) {
                 message.success('');
              }else {
                 message.error('');
              }
            })
        } else {
          message.warning('');
        }
      }
    })
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 8 }
      }
    };
    const richEditerLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 16,
          offset: 8
        }
      }
    };
    return (
      <Form onSubmit={this._handleSubmit.bind(this)}>
        <FormItem {...formItemLayout}>
          {getFieldDecorator('componentId')(
            <Input type="hidden" />
          )}
        </FormItem>
        <FormItem label="" {...formItemLayout}>
          {getFieldDecorator('componentName', {
            rules: [{ required: true, message: '' },{ validator: this._checkNname}],
          })(
            <Input placeholder="name"/>
          )}
        </FormItem>
        <FormItem label="" {...richEditerLayout}>
          {getFieldDecorator('componentDescribe', {
            rules: [{ required: true, message: '' }],
          })(
            <Input placeholder="describe" />
          )}
        </FormItem>
        <FormItem label="" {...richEditerLayout}>
          {getFieldDecorator('componentAttr', {
            rules: [{ required: true, message: '' }],
          })(
            <Select mode="multiple" placeholder="...">
              {this.state.attrlist}
            </Select>
          )}
        </FormItem>
        <FormItem label="" {...richEditerLayout} extra="32*321Mjpgpng">
          <ComponePictures handleChildValueChange={this._handleChildValueChange.bind(this)}/>
        </FormItem>
        <FormItem {...tailFormItemLayout}>
          <Button type="primary" htmlType="submit" style={{ width: 100 }}></Button>
          <Button onClick={this._handleReset.bind(this)} style={{ marginLeft: 8 }}></Button>
        </FormItem>
      </Form>
    );
  }
}

const ComponentForm = Form.create()(CompForm);
export default ComponentForm
