import React from 'react';
import { Form, Input, Button, Select, message } from 'antd';
import ReactUmeditor from './react-umeditor';
import URL from '../serverAPI.config';
const FormItem = Form.Item;
const Option = Select.Option;

class ExperForm extends React.Component {
  constructor () {
    super();//
    this.state = {
      compolist: [],
      content: ""
    };
  }
  componentDidMount () {
    const myFetchOptions = {
      method: 'GET',
      mode : 'cors',
    };
    fetch(URL.componentList,myFetchOptions)
      .then(res=>res.json().then(json=>{
        for (var i = 0; i < json.length;i++) {
          this.state.compolist.push(<Option key={json[i]._id.toString()}>{json[i].name}</Option>)
        }
      }))
  }

  _handleChildValueChange (content) {
    this.setState({ content: content })
  }

  _handleReset () {
    this.props.form.resetFields();
  }

  _handleSubmit (e) {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        const data = {
          id:values.experimentId,
          name:values.experimentName,
          introduction:values.experimentIntrod,
          components:values.experimentCompo,
          content:this.state.content
        }
        const myFetchOptions = {
          method: 'POST',
          headers: {'Content-Type':'application/json'},
          mode : 'cors',
          body: JSON.stringify(data)
        };
        console.log(data)
        if (this.state.content !== "") {
          fetch("http://localhost:8100/experiment/new",myFetchOptions)
            .then((res) => {
              if (res.ok) {
                 message.success('');
              }else {
                 message.error('');
              }
            })
        } else {
          message.warning('-_-!');
        }
      }
    })
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 3 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 8 }
      }
    };
    const submitLayout = {
      wrapperCol: {
        xs: { span: 24 },
        sm: {
          span: 8,
          offset: 3
        }
      }
    };
    const richEditerLayout = {
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 20 }
      }
    };
    const tailFormItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 3 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 20 }
      }
    };
    return (
      <div>
        <Form onSubmit={this._handleSubmit.bind(this)}>
          <FormItem {...formItemLayout}>
            {getFieldDecorator('experimentId')(
              <Input type="hidden" />
            )}
          </FormItem>
          <FormItem label="" {...formItemLayout}>
            {getFieldDecorator('experimentName', {
              rules: [{ required: true, message: '' }],
            })(
              <Input placeholder="name" />
            )}
          </FormItem>
          <FormItem label="" {...formItemLayout}>
          {getFieldDecorator('experimentIntrod', {
            rules: [{ required: true, message: '' }],
          })(
            <Input placeholder="introduction" />
          )}
          </FormItem>
          <FormItem label="" {...tailFormItemLayout}>
            {getFieldDecorator('experimentCompo', {
              rules: [{ required: true, message: '' }],
            })(
              <Select mode="multiple" placeholder="...">
                {this.state.compolist}
              </Select>
            )}
          </FormItem>
          <FormItem {...submitLayout}>
            <Button type="primary" htmlType="submit" style={{ width: 100 }}></Button>
            <Button onClick={this._handleReset.bind(this)} style={{ marginLeft: 8 }}></Button>
          </FormItem>
        </Form>
        <div style={{ marginBottom: 100 }}>
          <ReactUmeditor handleChildValueChange={this._handleChildValueChange.bind(this)} {...richEditerLayout}/>
        </div>
      </div>
    );
  }
}

const ExperimentForm = Form.create()(ExperForm);
export default ExperimentForm
